/* ====================== ﾑﾐｾﾐｷﾑﾐｰﾑﾐｾﾐｲﾑτ ﾐｲﾐｸﾑﾐｾﾑび ﾑひｵﾐｺﾑﾑひｾﾐｲﾐｾﾑ ﾑﾑびﾐｾﾐｺﾐｸ =======================*/
@mixin font-size($size, $height) {
  font-size: $size;
  line-height: calc($height / $size);
}

/* ============================ ﾐｴﾐｾﾐｴﾐｰﾑ ﾑﾐｺﾑﾑσｳﾐｻﾐｵﾐｽﾑ ﾐｿﾑﾐｵﾐｲﾐｴﾐｾﾐｵﾐｻﾐｵﾐｼﾐｵﾐｽﾑひｸ ﾑ ﾑﾐｵﾐｺﾑﾑ毛肖  =========================*/
@mixin pseudo($width, $height) {
  display: block;
  content: '';
  width: $width;
  height: $height;
  border-radius: 50%;
}

/* ============================ ﾐｴﾐｻﾑ ﾑﾐｾﾐｱﾐｾﾑひｸ ﾐｷ ﾑﾐｻﾐｵﾐｺﾑﾐｰﾐｼﾐｸ ===========================*/
/* ------- ﾑﾑひｰﾐｲﾐｸﾑび gap, ﾐｲﾐｸﾑﾑ孟ｲﾐｽﾑ紗 ﾐｾﾐｱ'ﾑ頒ｺﾑひｸ ﾐｿﾐｾ ﾐｳﾐｾﾑﾐｸﾐｷﾐｾﾐｽﾑひｰﾐｻﾑ ﾑ ﾐｿﾐｾ ﾐｲﾐｵﾑﾑひｸﾐｺﾐｰﾐｻﾑ (ﾐｷﾐｰ ﾐｷﾐｰﾐｼﾐｾﾐｲﾑﾐｵﾐｽﾐｽﾑ紹ｼ ﾐｿﾐｾ ﾑﾐｵﾐｽﾑびﾑ) ------- */
// ﾐｷﾐｽﾐｰﾑﾐｵﾐｽﾐｽﾑ null ﾐｽﾐｰ ﾐｼﾑ毛ﾑﾑ ﾐｷﾐｼﾑ孟ｽﾐｽﾐｾﾑ ﾐｿﾑﾐｸﾐｱﾐｸﾑﾐｰﾑ ﾐｿﾐｰﾑﾐｰﾐｼﾐｵﾑび (ﾑﾑびﾐｾﾐｺﾑ ﾐｺﾐｾﾐｴﾑ) ﾐｷ ﾐｼﾑ孟ｺﾑﾑ孟ｽﾑソn@mixin flex($gap, $x: center, $y: center) {
  display: flex;
  gap: $gap;
  justify-content: $x;
  align-items: $y;
}

/* ------- ﾑﾐｾﾐｷﾑﾐｰﾑﾐｾﾐｲﾑτ ﾐｿﾐｰﾑﾐｰﾐｼﾐｵﾑび flex-basis ------- */
// ($n - ﾐｺﾑ孟ｻﾑ糊ｺﾑ毛ﾑび ﾐｵﾐｻﾐｵﾐｼﾐｵﾐｽﾑび孟ｲ ﾑ ﾑﾑ紹ｴﾐｺﾑ; $gap - ﾐｷﾐｽﾐｰﾑﾐｵﾐｽﾐｽﾑ gap)
@mixin flex-basis($n, $gap) {
  flex-basis: calc((100% - ($gap * ($n - 1))) / $n);
}

/* ============================= ﾐｿﾐｾﾐｷﾐｸﾑﾑ孟ｾﾐｽﾑτ ﾐｵﾐｻﾐｵﾐｼﾐｵﾐｽﾑひｸ =======================*/
// ($position - ﾐｲﾐｸﾐｴ ﾐｿﾐｾﾐｷﾐｸﾑﾑ孟ｾﾐｽﾑσｲﾐｰﾐｽﾐｽﾑ (relative/absolute/fixed)
// $top-or-bottom - ﾑﾑひｰﾐｲﾐｸﾑび袴ﾑ ﾐｷﾐｽﾐｰﾑﾐｵﾐｽﾐｽﾑ top ﾐｰﾐｱﾐｾ bottom
// $y - ﾐｷﾐｼﾑ毛禍ｵﾐｽﾐｽﾑ ﾐｲﾑ孟ｴ ﾐｲﾐｵﾑﾑﾑ/ﾐｽﾐｸﾐｷﾑ ﾐｲ px
// $left-or-right - ﾑﾑひｰﾐｲﾐｸﾑび袴ﾑ ﾐｷﾐｽﾐｰﾑﾐｵﾐｽﾐｽﾑ left ﾐｰﾐｱﾐｾ right
// $x - ﾐｷﾐｼﾑ毛禍ｵﾐｽﾐｽﾑ ﾑﾐｿﾑﾐｰﾐｲﾐｰ/ﾐｷﾐｻﾑ孟ｲﾐｰ ﾐｲ px
@mixin position(
  $position: absolute,
  $top-or-bottom,
  $y: 0,
  $left-or-right,
  $x: 0
) {
  position: $position;
  #{$top-or-bottom}: $y;
  #{$left-or-right}: $x;
}

/* ============================ ﾑﾑひｰﾐｲﾐｸﾑび bg-picture ﾐｴﾐｻﾑ ﾑﾑ孟ｷﾐｽﾐｸﾑ ﾑﾐｵﾑひｸﾐｽﾐｰ-ﾐｴﾐｸﾑﾐｿﾐｻﾐｵﾑ厘ｲ ======================*/
/* ------- ﾐｴﾐｻﾑ ﾐｺﾐｰﾑﾑひｸﾐｽﾐｾﾐｺ ﾐｽﾐｰ ﾐｾﾐｴﾐｸﾐｽ ﾐｴﾐｵﾐｲﾐｰﾐｹﾑ ------- */
// $folder - ﾐｽﾐｰﾐｷﾐｲﾐｰ ﾐｿﾐｰﾐｿﾐｺﾐｸ, ﾐｴﾐｵ ﾐｷﾐｱﾐｵﾑﾑ孟ｳﾐｰﾑ飯び袴ﾑ ﾐｷﾐｾﾐｱﾑﾐｰﾐｶﾐｵﾐｽﾐｽﾑ. ﾐｯﾐｺﾑ禍ｾ ﾑ威ｻﾑ肖 ﾐｴﾐｾ ﾑﾐｰﾐｹﾐｻﾑ ﾑﾐｺﾐｻﾐｰﾐｴﾐｰﾑ ﾐｱﾑ孟ｻﾑ袴威ｵ ﾐｽﾑ孟ｶ 1 ﾐｿﾐｰﾐｿﾐｺﾑ (ﾐｽﾐｰﾐｿﾑﾐｸﾐｺﾐｻﾐｰﾐｴ images/backgroung/mobile),
// ﾑ ﾐｿﾐｰﾑﾐｰﾐｼﾐｵﾑびﾑ ﾐｲﾐｺﾐｰﾐｷﾑτ紗び袴ﾑ ﾑﾐｵﾑﾐｵﾐｷ / ﾐｲﾑﾑ ﾐｿﾐｰﾐｿﾐｺﾐｸ  ﾐｺﾑﾑ孟ｼ images (ﾐｽﾐｰﾐｿﾑﾐｸﾐｺﾐｻﾐｰﾐｴ backgroung/mobile)
// $picture-name - ﾐｽﾐｰﾐｷﾐｲﾐｰ ﾐｺﾐｰﾑﾑひｸﾐｽﾐｺﾐｸ ﾐｱﾐｵﾐｷ -min ﾑひｰ ﾑﾐｾﾑﾐｼﾐｰﾑび (ﾐｽﾐｰﾐｿﾑﾐｸﾐｺﾐｻﾐｰﾐｴ ﾑ紹ｺﾑ禍ｾ ﾐｺﾐｰﾑﾑひｸﾐｽﾐｺﾐｰ ﾐｽﾐｰﾐｷﾐｸﾐｲﾐｰﾑ飯び袴ﾑ "cow-min.png", ﾐｲﾐｺﾐｰﾐｷﾑτ飯ひｵ "cow")
// $format - ﾑﾐｾﾑﾐｼﾐｰﾑ ﾐｺﾐｰﾑﾑひｸﾐｽﾐｺﾐｸ (jpg ﾐｰﾐｱﾐｾ png)
@mixin bg-picture($folder, $picture-name, $format) {
  background-image: url('../images/#{$folder}/#{$picture-name}-min.#{$format}');

  @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
    background-image: url('../images/#{$folder}/#{$picture-name}@2x-min.#{$format}');
  }

  @media (min-device-pixel-ratio: 3),
    (min-resolution: 288dpi),
    (min-resolution: 3dppx) {
    background-image: url('../images/#{$folder}/#{$picture-name}@3x-min.#{$format}');
  }
}

/* ============================= aﾐｽﾑ孟ｼﾑτ ﾐｲﾐｻﾐｰﾑﾑひｸﾐｲﾐｾﾑﾑび =======================*/
/* ------- ﾐｾﾐｴﾐｽﾑ ﾐｲﾐｻﾐｰﾑﾑひｸﾐｲﾑ毛ﾑび ------- */
@mixin animation($property) {
  transition: $property $animation;
}

/* ------- ﾐｴﾐｲﾑ ﾐｲﾐｻﾐｰﾑﾑひｸﾐｲﾐｾﾑﾑび ------- */
@mixin animation-m($property-1, $property-2) {
  transition: $property-1 $animation, $property-2 $animation;
}

/* ------- ﾑびﾐｸ ﾐｲﾐｻﾐｰﾑﾑひｸﾐｲﾐｾﾑﾑび ------- */
@mixin animation-l($property-1, $property-2, $property-3) {
  transition: $property-1 $animation, $property-2 $animation,
    $property-3 $animation;
}

/* ============================ ﾑﾑひｲﾐｾﾑﾑ紗 ﾐｼﾐｵﾐｴﾑ孟ｰﾐｿﾑﾐｰﾐｲﾐｸﾐｻﾐｰ ======================*/
// $max-or-min - ﾑﾑひｰﾐｲﾐｸﾑび袴ﾑ ﾐｷﾐｽﾐｰﾑﾐｵﾐｽﾐｽﾑ max ﾐｰﾐｱﾐｾ min
// $device - ﾐｲﾐｺﾐｰﾐｷﾑτ飯び袴ﾑ ﾐｽﾐｰﾐｷﾐｲﾐｰ ﾑひｸﾐｿﾑ ﾐｴﾐｵﾐｲﾐｰﾐｹﾑﾐｰ (phone/tablet/desktop)
@mixin media($max-or-min: min, $device) {
  @if $max-or-min == max {
    @media screen and (#{$max-or-min}-width: (map-get($breakpoints, '#{$device}') - 0.5px)) {
      @content;
    }
  } @else {
    @media screen and (#{$max-or-min}-width: map-get($breakpoints, '#{$device}')) {
      @content;
    }
  }
}

/* =================== ﾑﾑひｸﾐｻﾑ孟ｷﾑτ ﾐｺﾐｽﾐｾﾐｿﾐｺﾐｸ ﾑ ﾑﾑ孟ｷﾐｽﾐｸﾑ ﾑﾑひｰﾐｽﾐｰﾑ =================*/
// $type - ﾐｾﾐｱﾐｾﾐｲ'ﾑ紹ｷﾐｺﾐｾﾐｲﾐｵ ﾐｷﾐｽﾐｰﾑﾐｵﾐｽﾐｽﾑ, ﾐｾﾐｱﾐｸﾑﾐｰﾑ ﾑひｸﾐｿ ﾑﾑひｸﾐｻﾑ孟ｷﾐｰﾑﾑ毛. ﾐ厘ｽﾐｰﾑﾐｵﾐｽﾐｽﾑ (color, stroke, text) ﾐｾﾐｱﾐｸﾑﾐｰﾑ飯び袴ﾑ ﾐｷ ﾐｽﾐｰﾑﾑびσｿﾐｽﾐｸﾑ:
// ﾐｯﾐｺﾑ禍ｾ ﾐｺﾐｽﾐｾﾐｿﾐｺﾐｰ ﾐｼﾐｰﾑ ﾑﾐｾﾐｽﾐｾﾐｲﾐｸﾐｹ ﾐｺﾐｾﾐｻﾑ毛 ﾑ ﾐｿﾑﾐｵﾐｲﾐｴﾐｾﾐｵﾐｻﾐｵﾐｼﾐｵﾐｽﾑ - color;
// ﾐｯﾐｺﾑ禍ｾ ﾐｺﾐｽﾐｾﾐｿﾐｺﾐｰ ﾐｼﾐｰﾑ ﾐｺﾐｾﾐｻﾑ糊ｾﾑﾐｾﾐｲﾑ ﾑﾐｰﾐｼﾐｺﾑ ﾑ ﾐｿﾑﾐｵﾐｲﾐｴﾐｾﾐｵﾐｻﾐｵﾐｼﾐｵﾐｽﾑ - stroke;
// ﾐｯﾐｺﾑ禍ｾ ﾑひｵﾐｺﾑﾑ ﾐｼﾑ毛ﾑひｸﾑび ﾑび孟ｻﾑ糊ｺﾐｸ ﾐｿﾐｾﾑﾐｸﾐｻﾐｰﾐｽﾐｽﾑ ﾐｲﾑ孟ｴﾐｿﾐｾﾐｲﾑ孟ｴﾐｽﾐｾﾐｳﾐｾ ﾐｺﾐｾﾐｻﾑ糊ｾﾑﾑ - text
// $size - ﾑﾐｾﾐｷﾐｼﾑ毛 ﾑ尉ﾐｸﾑﾑび ﾐｲ ﾐｿﾑ孟ｺﾑﾐｵﾐｻﾑ肖 (ﾐｿﾐｾ ﾐｼﾐｰﾐｺﾐｵﾑび)
// $height - ﾑﾐｾﾐｷﾐｼﾑ毛 ﾐｲﾐｸﾑﾐｾﾑひｸ ﾑﾑびﾐｾﾐｺﾐｸ ﾐｲ ﾐｿﾑ孟ｺﾑﾐｵﾐｻﾑ肖 (ﾐｿﾐｾ ﾐｼﾐｰﾐｺﾐｵﾑび)
@mixin button-styles($type, $size: 14px, $height: 18px) {
  padding: 12px 34px;
  border-radius: 10px;

  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  color: var(--text-black);
  font-weight: 600;
  font-size: $size;
  line-height: calc($height / $size);

  transition: background-color $animation, background-image $animation;

  &::after {
    display: block;
    content: '';
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-image: url(../images/button-bracket-black.svg);
    background-size: contain;
    background-repeat: no-repeat;
    margin-left: 6px;
  }

  &:focus {
    color: var(--text-white);
    outline: none;

    &::after {
      background-image: url(../images/button-bracket-white.svg);
    }
  }

  @if $type == color {
    background-color: var(--button-yellow);
    transition: background-color $animation, filter $animation;

    &:hover,
    &:focus {
      background-color: var(--button-green);
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
    &:disabled {
      background-color: var(--button-grey);
    }
  } @else if $type == stroke {
    background-color: transparent;
    border: 2px solid var(--button-yellow);
    transition: border $animation, filter $animation;

    &:hover,
    &:focus {
      border: 2px solid var(--button-green);
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
    &:disabled {
      border: 2px solid var(--button-grey);
    }
  } @else if $type == text {
    background-color: transparent;
    border: none;
    color: var(--button-yellow);
    transition: color $animation, filter $animation;
    &::after {
      display: none;
    }

    &:hover,
    &:focus {
      color: var(--button-green);
      filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }
    &:disabled {
      color: var(--button-grey);
    }
  }
}
